.main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
  color: white;
}

.main img {
  width: 45%;
}

.content {
  width: 40%;
  padding-top: 2em;
}

.content p {
  padding-top: 1em;
}
button {
  width: 120px;
  padding: 1.2em;
  outline: none;
  border-radius: 30px;
  color: purple;
  cursor: pointer;
  border: none;
  margin-top: 2em;
  font-weight: bold;
}

@media (max-width: 780px) {
  .main {
    flex-direction: column;
    justify-content: center;
  }

  .main img {
    margin: 0 auto;
  }

  .content {
    width: 100%;
  }
}
